import { createNamespacedHelpers } from 'vuex';
import debounce from 'lodash.debounce';

import {
  actions as contactsActions,
} from '@modules/contacts/constants';

const {
  mapState: mapContactsState,
  mapActions: mapContactsActions,
} = createNamespacedHelpers('contacts');

export default {
  computed: {
    ...mapContactsState(['contactsEmails']),
  },
  methods: {
    ...mapContactsActions({
      getContactsEmails: contactsActions.GET_CONTACTS_EMAILS,
    }),
  },
  mounted() {
    this.getContactsEmails(null);
  },
  created() {
    this.getContactsEmails = debounce(this.getContactsEmails, 250);
  },
  beforeDestroy() {
    if (this.getContactsEmails.clear) this.getContactsEmails.clear();
  },
};
